<template>
  <div class="voting-wrapper">
    <div class="voting-container">
      <div class="header">
        {{ questionData.question }}
      </div>
      <div class="subheader">
        {{ questionData.type === 1 ? 'Можно отметить только один пункт' : 'Можно отметить несколько пунктов' }}
      </div>
      <div
        v-if="!isVoted"
        class="answers-container"
      >
        <div class="answer-item"
          v-for="answer in questionData.variants" 
          :key="answer.id"
          @click="answerClick(answer.id)"
          :class="{'background-blue': selectedAnswerIds.includes(answer.id)}"
        >
          <div class="answer-item-text">
            {{ answer.name }}
          </div>
        </div>
      </div>

      <div
        v-if="isVoted"
        v-html="'Спасибо!<br>Ваш голос учтен'"
        class="is-voted-text"
      >
      </div>

      <div class="voting-button-container">
        <div
          v-if="!isVoted"
          @click="vote"
          :class="{'enable': selectedAnswerIds.length > 0}"
          class="voting-button"
        >
          ПРОГОЛОСОВАТЬ
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data() {
    return {
      serverURL: "https://tech2.med-game.ru/api/v1",
      questionData: {},
      selectedAnswerIds: [],
      isVoted: false
    }
  },
  methods: {
    getQuestionData(){
      axios({
        method: 'get',
        url: `${this.serverURL}/voting/${this.$route.params.sessionId}`,
      })
      .then((questionData) => {
        this.questionData = questionData.data.data
      })
      .catch((error) => {
        console.log('Ошибка:');
        console.log(error)
      })
    },
    answerClick(id){
      if (this.questionData.type === 1) {
        this.selectedAnswerIds = [id];
      } else {
        const index = this.selectedAnswerIds.indexOf(id);
        if (index === -1) {
          this.selectedAnswerIds.push(id);
        } else {
          this.selectedAnswerIds.splice(index, 1);
        }
      }
    },
    vote(){
      this.selectedAnswerIds.forEach((id) => {
        axios({
          url: `${this.serverURL}/voting/${this.$route.params.sessionId}/answer`,
          method: 'post',
          data: {
            "variant_id": id
          }
        })
        .then(() => {
          this.isVoted = true;
          localStorage.setItem(this.$route.params.sessionId, 1);
        })
        .catch(function (error) {
          console.log(error);
        });
      });
    }
  },
  mounted() {
    this.getQuestionData()
    const value = localStorage.getItem(this.$route.params.sessionId)
    if (value == 1) {
      this.isVoted = true
    }
    else {
      this.isVoted = false
    }
  },
}
</script>

<style>
body{
  margin: 0;
}
.voting-wrapper{
  display: flex;
  justify-content: center;
  /* margin: 0, auto; */
  background: #c5eaff;
  height: 100vh;
  width: 100vw;
  overflow-y: auto;
}
.voting-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #c5eaff;
}
.header{
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 27px;
  text-align: center;
  color: #224A81;
  margin-top: 50px;
  margin-bottom: 20px;
}
.subheader{
  display: flex;
  justify-content: center;
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  color: #000000;
  margin-bottom: 50px;
}
.answers-container{
  flex-direction: column;
  align-items: center;
  row-gap: 10px;
}
.answers-container, .subheader {
  display: flex;
  margin-bottom: 50px;
}
.answer-item{
  display: flex;
  align-items: center;
  width: 80vw;
  min-height: 43px;
  background: #ebf8ff;
  border-radius: 5px;
  color: #000;
  cursor: pointer;
}
.background-blue{
  background: #285798;
  color: #FFFFFF;
}
.answer-item-text{
  margin-left: 30px;
  margin-right: 30px;
  margin-top: 5px;
  margin-bottom: 5px;
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
}
.voting-button-container{
  display: flex;
  align-items: end;
  flex-grow: 1;
}
.voting-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 55px;
  background: #a8a8a8;
  border-radius: 5px;
  border-radius: 20px;
  cursor: pointer;
  color: #fff;
  transition-duration: .2s;
  pointer-events: none;
  margin-bottom: 50px;
}
.voting-button, .voting-button-text {
  font-family: 'Open Sans' ,sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  text-align: center;
}
.enable {
  pointer-events: auto;
  background: #3180c9;;
  color: #FFFFFF;
}
.is-voted-text{
  display: flex;
  justify-content: center;
  margin-top: 120px;
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  color: #000000;
}

</style>